<template>
  <v-container>

    <v-row class="text-center">
      <v-col cols="12" class="d-flex message-block">
        <v-card
            min-height="450px"
            elevation="5"
            max-width="900"
            width="100%"
            class="ma-auto pa-5"
        >

          <v-card
              width="100%"
              height="410px"
              elevation="0"
              class="mt-6"
              :class="{['overflow-y-auto pr-2']: comments.length >= 3}"
          >
            <template v-for="(comment, index) in comments">

              <!--Обычный коммент-->
              <v-card
                  :key="comment.id"
                  elevation="0"
                  v-if="!comment.isDelete"
              >
                <v-list-item three-line class="pr-0">
                  <v-list-item-avatar
                      left
                      color="#C4C4C4"
                      size="36"
                  >
                    AA
                  </v-list-item-avatar>
                  <v-list-item-content>

                    <div class="text-subtitle-2 text-left mb-1">
                      <span :style="getRandomColor(comment.id)">{{ comment.id }}</span>
                      <span> {{ formatDate(comment.dateCreated) }}</span>
                    </div>

                    <template>
                      <div class="text-justify mb-0 comment-text">
                        {{ comment.commentText }}
                      </div>
                    </template>
                  </v-list-item-content>

                </v-list-item>

                <v-card-actions class="pa-0 ml-15 flex-wrap">
                  <v-btn
                      text
                      :x-small="width <= 768"
                      color="#000000"
                      class="text-capitalize text-body-1 font-weight-bold"
                      @click.stop="showAnswerInput(comment.id,comment.path)"
                  >
                    {{ isAnswer[comment.id] ? "Отменить" : "Ответить" }}
                  </v-btn>

                  <v-btn
                      text
                      :x-small="width <= 768"
                      color="rgba(0,0,0,.7)"
                      class="text-capitalize text-body-1 ml-0"
                  >
                    Изменить
                  </v-btn>

                  <v-btn
                      text
                      :x-small="width <= 768"
                      color="rgba(0,0,0,.7)"
                      class="text-capitalize text-body-1 ml-0"
                      @click.stop="openConfirm(comment.id)"
                  >
                    Удалить
                  </v-btn>

                </v-card-actions>

              </v-card>

              <!--Удаленный коммент-->
              <v-card
                  :key="comment.id"
                  elevation="0"
                  v-else
              >
                <v-list-item three-line class="pr-0">
                  <v-list-item-avatar
                      left
                      color="#C4C4C4"
                      size="36"
                  >
                    AA
                  </v-list-item-avatar>
                  <v-list-item-content>

                    <div class="text-subtitle-2 text-left mb-1">
                      <span :style="getRandomColor(comment.id)">{{ comment.id }}</span>
                    </div>

                    <template>
                      <div class="text-justify mb-0 comment-text__deleted">
                        Комментарий удален &nbsp;{{ formatDeleteDate(comment.dateModified) }}
                      </div>
                    </template>
                  </v-list-item-content>

                </v-list-item>

              </v-card>

              <!--Форма ответа на коммент-->
              <v-card
                  class="d-flex flex-column"
                  elevation="0"
                  :key="'comment_' + comment.id"
                  v-if="isAnswer[comment.id]"
              >
                <v-list-item
                    class="pr-0"
                    dense
                >
                  <v-list-item-avatar
                      class="mb-0"
                      left
                      color="#C4C4C4"
                      size="36"
                  >
                    AA
                  </v-list-item-avatar>
                  <v-textarea
                      class="mt-2"
                      rows="1"
                      solo
                      placeholder="Добавить комментарий..."
                      outlined
                      rounded
                      hide-details
                      no-resize
                      dense
                      auto-grow
                      wrap="hard"
                      maxlength="3000"
                      v-model.trim="answerCommentObj.comment_text"
                  >

                  </v-textarea>
                </v-list-item>

                <v-row class="ma-0 mt-3 align-center justify-space-between">

                  <div class="ml-15"></div>

                  <v-btn
                      class="black--text text-capitalize text-body-1 font-weight-medium"
                      style="z-index: 1;"
                      elevation="4"
                      color="#E8D3E8"
                      rounded
                      font-size="1.2em"
                      @click.stop="sendAnswerComment"
                  >
                    Отправить
                  </v-btn>
                </v-row>


              </v-card>

              <!--Вложенный коммент-->
              <AnswerComment
                  :key="comment.id+'_child'"
                  :index="index"
                  :comment="comment"
                  @sendAnswerComment="sendAnswerComment"
              />

            </template>
          </v-card>

          <v-card
              class="d-flex flex-column"
              elevation="0"
          >
            <v-list-item
                class="pr-0"
                dense
            >
              <v-list-item-avatar
                  class="mb-0"
                  left
                  color="#C4C4C4"
                  size="36"
              >
                AA
              </v-list-item-avatar>
              <v-textarea
                  class="mt-2"
                  rows="1"
                  solo
                  placeholder="Добавить комментарий..."
                  outlined
                  rounded
                  hide-details
                  no-resize
                  dense
                  auto-grow
                  wrap="hard"
                  maxlength="3000"
                  v-model.trim="commentObj.comment_text"
              >

              </v-textarea>
            </v-list-item>

            <v-row class="ma-0 mt-3 align-center justify-space-between">

              <div class="ml-15">

              </div>

              <v-btn
                  class="black--text text-capitalize text-body-1 font-weight-medium"
                  elevation="4"
                  color="#E8D3E8"
                  rounded
                  font-size="1.2em"
                  @click.stop="sendComment"
              >
                Отправить
              </v-btn>
            </v-row>


          </v-card>

        </v-card>
      </v-col>
    </v-row>

    <ConfirmModal :dialog="isConfirm" @deleteComment=delComment @closeConfirm="isConfirm=false"/>

  </v-container>
</template>

<script>
import {mapActions, mapGetters} from "vuex";
import ConfirmModal from "@/components/ConfirmModal";
import AnswerComment from "@/components/AnswerComment";

export default {
  name: "Comments",
  components: {ConfirmModal, AnswerComment},
  comments: {
    ConfirmModal,
  },

  data() {
    return {
      commentObj: {
        path: "",
        page_id: "1",
        comment_text: "",
        is_private: true,
        attachment: [],
        user_id: "78",
      },
      answerCommentObj: {
        path: "",
        page_id: "1",
        comment_text: "",
        is_private: true,
        attachment: [],
        user_id: "78",
      },
      colors: ["#0038FF", "#FE0000", "#28A015"],
      width: 0,
      isConfirm: false,
      commentId: 0,
      delDate: 0,
      isAnswer: {},
      answerPath: "",
      answerId: null,
    };

  },

  methods: {
    ...mapActions([
      "fetchComments",
      "SET_COMMENT",
      "DEL_COMMENT",
    ]),

    /**
     * Метод отправки коментария
     */
    async sendComment() {
      if (this.commentObj.comment_text !== "") {
        await this.SET_COMMENT(this.commentObj);

        this.commentObj = {
          path: "",
          page_id: "1",
          comment_text: "",
          is_private: true,
          attachment: [],
          user_id: "78",
        };
      }
    },

    /**
     * Метод отправки ответа на комментарий
     * @returns {Promise<void>}
     */
    async sendAnswerComment() {
      if (this.answerCommentObj.comment_text !== "") {
        this.$set(this.answerCommentObj, "parent_path", this.answerPath);
        await this.SET_COMMENT(this.answerCommentObj);

        this.answerCommentObj = {
          parent_path: "",
          page_id: "1",
          comment_text: "",
          is_private: true,
          attachment: [],
          user_id: "78",
        };
        for (let itemId in this.isAnswer) {
          this.isAnswer[itemId] = false;
        }
      }
    },

    /**
     * Метод удаления комментария
     * @returns {Promise<void>}
     */
    async delComment() {
      await this.DEL_COMMENT(this.commentId);
      this.isConfirm = false;
    },

    /**
     * Метод форматирования даты
     * @param date
     * @returns {string}
     */
    formatDate(date) {
      date += "Z";
      let oldDate = new Date(date);
      return oldDate.toLocaleString("ru-RU");
    },

    /**
     * Метод форматирования даты удаления
     * @param date
     * @returns {string}
     */
    formatDeleteDate(date) {
      let oldDate = new Date(date);
      return oldDate.toLocaleString("ru-RU");
    },

    /**
     * Метод установления рандомного цвета для имени пользователя
     * @returns {{color: string}}
     */
    getRandomColor() {
      return {
        color: this.colors[Math.floor(Math.random() * this.colors.length)],
      };
    },

    /**
     * Метод установки рандомного цвета текста для имени пользователя
     */
    handleResize() {
      this.width = window.innerWidth;
    },

    /**
     * Метод открытия модального окна подтверждения удаления
     * @param id
     */
    openConfirm(id) {
      this.commentId = id;
      this.isConfirm = true;
    },

    /**
     * Метод отображения инпута для ответа
     * @param id
     */
    showAnswerInput(id, path) {
      this.answerPath = path;
      this.answerId = id;
      if (!this.isAnswer[id]) {
        for (let itemId in this.isAnswer) {
          this.isAnswer[itemId] = false;
        }
        this.$set(this.isAnswer, id, false);
      }
      this.isAnswer[id] = !this.isAnswer[id];
    },
  },

  computed: {
    ...mapGetters({
      comments: "COMMENTS",
    }),
  },

  mounted() {
    this.fetchComments("1");
  },

  created() {
    window.addEventListener("resize", this.handleResize);
    this.handleResize();

  },
  destroyed() {
    window.removeEventListener("resize", this.handleResize);
  },

};
</script>

<style lang="scss">
.message-block {
  height: 100vh;
}

.comment-text {
  width: 100%;
  min-height: 37px;
  white-space: pre-line;
  background: #FFFFFF;
  border: 1px solid rgba(0, 0, 0, 0.3);
  box-sizing: border-box;
  padding: 8px 12px;
  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 24px;

  &__deleted {
    width: 100%;
    min-height: 37px;
    white-space: pre-line;
    background: #FFFFFF;
    box-sizing: border-box;
    border-radius: 5px;
    padding: 8px 12px;
    color: #454545;
  }
}

.v-btn__content {
  letter-spacing: 0;
}

@media (max-width: 768px) {
  .v-btn__content {
    font-size: 14px;
  }
}
</style>
