import Vue from "vue";
import Vuex from "vuex";
import api from "@/utils/api";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    comments: [],
  },
  mutations: {
    GET_COMMENTS(state, comments) {
      let result =  getParentIds(comments);
      result = getNormalStruct(comments);
      Vue.set(state, "comments", result);
    },

    REMOVE_COMMENT(state, id) {
      state.comments.forEach(comment => {
        if (comment.id === id) {
          comment.isDelete = true;
          comment.dateModified = new Date;
        }
      });
    },

  },
  actions: {
    async fetchComments({commit}, payload) {
      try {
        const {data} = await api.get("/v1/comment", {params: {page_id: payload}});
        commit("GET_COMMENTS", data);
        return data;
      } catch (e) {
        console.log(e);
      }
    },

    async SET_COMMENT({dispatch}, payload) {
      try {
        await api.post("/v1/comment", payload);
        dispatch("fetchComments", "1");
      } catch (e) {
        console.log(e);
      }
    },

    async DEL_COMMENT({commit}, payload) {
      try {
        await api.delete("/v1/comment", {params: {comment_id: payload}});
        commit("REMOVE_COMMENT", payload);
      } catch (e) {
        console.log(e);
      }
    },
  },
  getters: {
    COMMENTS: state => state.comments,
  },
});

//Добавить parentId
function getParentIds(arr){
  arr.forEach(e => {
    if (e.path.split(".").length == 1) {
      e.parentId = null;
    } else {
      e.parentId = e.path.split(".").slice(0, -1).join(".");
    }
  });
  return arr;
}
//Добавление массива children если parentId не равен null, и добавление в него вложенные комментарии
function getNormalStruct(arr, parent = null) {
  let res = arr.filter(e => e.parentId == parent);
  res.forEach(e => e.children = getNormalStruct(arr, e.path));
  return res;
}

