<template>
  <div>


    <template v-for="child in comment.children">

      <!--ответ на комментарий-->
      <v-card
          elevation="0"
          :key="child.id+'_children'"
          :style="{width: (100 - (child.parentId.split('.').length * 5))  + '%', marginLeft: 'auto' }"
      >
        <v-list-item three-line class="pr-0">
          <v-list-item-avatar
              left
              color="#C4C4C4"
              size="36"
          >
            AA
          </v-list-item-avatar>
          <v-list-item-content>

            <div class="text-subtitle-2 text-left mb-1">
              <span :style="getRandomColor(child.id)">{{ child.id }}</span>
              <span> {{ formatDate(child.dateCreated) }}</span>
            </div>

            <template>
              <div class="text-justify mb-0 comment-text">
                {{ child.commentText }}
              </div>
            </template>
          </v-list-item-content>

        </v-list-item>

        <v-card-actions class="pa-0 ml-15 flex-wrap">
          <v-btn
              text
              :x-small="width <= 768"
              color="#000000"
              class="text-capitalize text-body-1 font-weight-bold"
              @click.stop="showAnswerInput(child.id, child.path)"
          >
            {{ isAnswer[child.id] ? "Отменить" : "Ответить" }}
          </v-btn>

          <!--        <v-btn-->
          <!--            text-->
          <!--            :x-small="width <= 768"-->
          <!--            color="rgba(0,0,0,.7)"-->
          <!--            class="text-capitalize text-body-1 ml-0"-->
          <!--        >-->
          <!--          Изменить-->
          <!--        </v-btn>-->


          <!--        <v-btn-->
          <!--            text-->
          <!--            :x-small="width <= 768"-->
          <!--            color="rgba(0,0,0,.7)"-->
          <!--            class="text-capitalize text-body-1 ml-0"-->
          <!--            @click.stop="openConfirm(child.id)"-->
          <!--        >-->
          <!--          Удалить-->
          <!--        </v-btn>-->

        </v-card-actions>

      </v-card>
      <!--форма ответа на комментарий-->
      <v-card
          :style="{width: (100 - (child.parentId.split('.').length * 5))  + '%', marginLeft: 'auto' }"
          :key="child.id"
          class="d-flex flex-column"
          elevation="0"
          v-if="isAnswer[child.id]"
      >
        <v-list-item
            class="pr-0"
            dense
        >
          <v-list-item-avatar
              class="mb-0"
              left
              color="#C4C4C4"
              size="36"
          >
            AA
          </v-list-item-avatar>
          <v-textarea
              class="mt-2"
              rows="1"
              solo
              placeholder="Добавить комментарий..."
              outlined
              rounded
              hide-details
              no-resize
              dense
              auto-grow
              wrap="hard"
              maxlength="3000"
              v-model.trim="answerCommentObj.comment_text"
          >

          </v-textarea>
        </v-list-item>

        <v-row class="ma-0 mt-3 align-center justify-space-between">

          <div class="ml-15"></div>

          <v-btn
              class="black--text text-capitalize text-body-1 font-weight-medium"
              style="z-index: 1;"
              elevation="4"
              color="#E8D3E8"
              rounded
              font-size="1.2em"
              @click.stop="sendAnswerComment"
          >
            Отправить
          </v-btn>
        </v-row>


      </v-card>
      <!--рекурсивный компонент-->
      <AnswerComment
          v-for="(item, subindex) in comment.children"
          :key="item.id + '_' + child.id"
          :index="subindex"
          v-bind:comment="item"
          :parentItem="comment"
      />

    </template>
  </div>
</template>

<script>
import {mapActions} from "vuex";
export default {
  name: "AnswerComment",
  props: {
    comment: {
      type: Object,
      required: true,
    },
    index: {
      type: Number,
      required: true,
    },
    parentItem: {
      required: false,
    },
  },
  data() {
    return {
      colors: ["#0038FF", "#FE0000", "#28A015"],
      width: 0,
      isAnswer: {},
      answerCommentObj: {
        path: "",
        page_id: "1",
        comment_text: "",
        is_private: true,
        attachment: [],
        user_id: "78",
      },
      answerPath: "",
    };
  },
  methods: {
    ...mapActions([
      "SET_COMMENT",
    ]),
    /**
     * Метод установления рандомного цвета для имени пользователя
     * @returns {{color: string}}
     */
    getRandomColor() {
      return {
        color: this.colors[Math.floor(Math.random() * this.colors.length)],
      };
    },

    /**
     * Метод форматирования даты
     * @param date
     * @returns {string}
     */
    formatDate(date) {
      date += "Z";
      let oldDate = new Date(date);
      return oldDate.toLocaleString("ru-RU");
    },

    /**
     * Метод установки рандомного цвета текста для имени пользователя
     */
    handleResize() {
      this.width = window.innerWidth;
    },

    /**
     * Метод отображения инпута для ответа
     * @param id
     */
    showAnswerInput(id, path) {
      this.answerPath = path;
      this.answerId = id;
      if (!this.isAnswer[id]) {
        for (let itemId in this.isAnswer) {
          this.isAnswer[itemId] = false;
        }
        this.$set(this.isAnswer, id, false);
      }
      this.isAnswer[id] = !this.isAnswer[id];
    },

    async sendAnswerComment() {
      if (this.answerCommentObj.comment_text !== "") {
        this.$set(this.answerCommentObj, "parent_path", this.answerPath);
        await this.SET_COMMENT(this.answerCommentObj);

        this.answerCommentObj = {
          parent_path: "",
          page_id: "1",
          comment_text: "",
          is_private: true,
          attachment: [],
          user_id: "78",
        };
        for (let itemId in this.isAnswer) {
          this.isAnswer[itemId] = false;
        }
      }
    },
  },
  created() {
    window.addEventListener("resize", this.handleResize);
    this.handleResize();
  },
  destroyed() {
    window.removeEventListener("resize", this.handleResize);
  },
};
</script>
